import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TrashComponent } from './trash/trash.component';
import { SentListComponent } from './sent-list/sent-list.component';
import { InboxListComponent } from './inbox-list/inbox-list.component';
import { ComposeMessageComponent } from './compose-message/compose-message.component';




const routes: Routes = [
	{ path: '', redirectTo: 'compose', pathMatch: 'full'},
	{ path: 'inbox-list', component: InboxListComponent},
	{ path: 'compose', component: ComposeMessageComponent},
	{ path: 'sent', component: SentListComponent},
	{ path: 'trash', component: TrashComponent},
	{ path: '**', redirectTo: 'compose', pathMatch: 'full'}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class InboxRouting { }
